<template>
  <div>
    <div class="wrapper">
      <input type="text" v-model="st" @keydown.enter="addItems" />
      <div v-for="(item, index) in items" :key="index">{{ item }}</div>
    </div>
  </div>
</template>

<script>

export default {
  name: "index",

  data() {
    return {
      items: [],
      st: "",
    };
  },

  methods: {
    addItems() {
      this.items.push(this.st);
      this.st = "";
    },
  },

  watch: {
    items: {
      handler() {
        console.log("watch");
        // localStorage.setItem("items", JSON.stringify(this.items));
         this.$cookies.set("items", JSON.stringify(this.items));

      },
    },
  },

  mounted() {
    console.log('mounted');
    const items = this.$cookies.get("items"); 
    if (items) {
      // this.items = JSON.parse(localStorage.getItem("items"));
      this.items = JSON.parse(this.$cookies.get('items'));
    }
  },
};
</script>

<style lang="scss" scoped>
@media (max-width: 768px) and (min-width: 426px) {
  .wrapper {
    width: 400px;
  }
}
@media (max-width: 425px) {
  .wrapper {
    width: 300px;
  }
}
</style>